.navbar {
    min-height: 6rem;
    background-color: #563d7c;
    position: fixed;
    z-index: 99;
    width: 100%;
}

h1 {
    color: white;
    line-height: 1.5rem;
    font-family: "Anton", sans-serif;
}

.row {
    margin-top: 7rem;
}

.img-thumbnail {
    margin-bottom: 5%;
    height: 58.248484848%;
    width: 100%;
}

.img-thumbnail:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

img {
    max-width: 100%;
}


  


